<template>
  <div class="container">
    <div class="hero">
      <nav>
        <img src="../assets/logo.png" alt="logo" />
        <h2>MOCK SERVER</h2>
      </nav>
      <div class="main">
        <h1>MOCK SERVER</h1>
        <p>Easily recreate server responses in a few clicks.</p>
        <button @click="login">GET STARTED</button>
      </div>
    </div>
  </div>
</template>

<script>
import Localbase from "localbase";
let db = new Localbase("api");
export default {
  name: "Home",
  mounted() {
    this.autologin();
  },
  methods: {
    async login() {
      fetch(`${process.env.VUE_APP_URI}user/add`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
      })
        .then((response) => response.json())
        .then((json) => {
          this.$store.dispatch("setToken", json.data.id);
          db.collection("users").doc("key").set({
            token: json.data.id,
          });
          this.$router.replace({ name: "Dashboard" });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    autologin() {
      db.collection("users")
        .doc("key")
        .get()
        .then((d) => {
          if (d && d.token !== null) {
            this.$store.dispatch("setToken", d.token);
            this.$router.replace({ name: "Dashboard" });
          }
        });
    },
  },
};
</script>

